.stt {
  position: fixed;
  right: 0px;
  bottom: 130px;
  display: flex;
  width: 37px;
  height: 37px;
  padding: 5px;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease-in-out;
  @media screen and (min-width: 1024px) {
    right: 30px;
    width: 50px;
    height: 50px;
  }
}

.stt:hover {
  opacity: 1;
}

.stt__active {
  visibility: visible;
  opacity: 0.5;
}

