.stt__circle__black {
  background-color: var(--accent-color-orange);
  border-radius: 50%;
}

.stt__circle__black:before {
  position: absolute;
  top: 50%;
  left: 40%;
  display: block;
  width: 20px;
  height: 5px;
  content: '';
  background-color: #fff;
  transform: translate(0, -50%) rotate(45deg);
}

.stt__circle__black:after {
  position: absolute;
  top: 50%;
  right: 40%;
  display: block;
  width: 20px;
  height: 5px;
  content: '';
  background-color: #fff;
  transform: translate(0, -50%) rotate(-45deg);
}

.stt__emoji {
  border-radius: 50%;
  opacity: 1;
}

.stt__emoji:before {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 36px;
  content: '\1F446';
}
